#!/usr/bin/env ts-node
import type { AxiosError } from 'axios'
import type { ErrorEvent } from 'eventsource'
import process from 'process'
import axios from 'axios'
import { EventSource } from 'eventsource'
import yargs from 'yargs'
import { hideBin } from 'yargs/helpers'

const BASE_URL = 'http://0.0.0.0:8888'

// ÂëΩ‰ª§Á†Å (‰∏é src/communication.ts ‰∏ÄËá¥)
const CommandCode = {
  START: 1,
  HEARTBEAT: 2,
  STOP: 3,
}

async function sendCommand(command: number, data = {}) {
  try {
    const response = await axios.post(`${BASE_URL}/command`, {
      c: command,
      d: data,
    })
    console.log('‚úÖ  Response:')
    console.log(response.data)
  }
  catch (error: any) {
    console.error('‚ùå  ËØ∑Ê±ÇÂ§±Ë¥•:')
    if (axios.isAxiosError(error)) {
      const axiosError = error as AxiosError
      if (axiosError.response) {
        // ÊúçÂä°Âô®ËøîÂõû‰∫ÜÈîôËØØÁä∂ÊÄÅÁ†Å
        console.error(`  - Áä∂ÊÄÅÁ†Å: ${axiosError.response.status}`)
        console.error('  - ÂìçÂ∫îÊï∞ÊçÆ:', axiosError.response.data)
      }
      else if (axiosError.request) {
        // ËØ∑Ê±ÇÂ∑≤ÂèëÂá∫Ôºå‰ΩÜÊ≤°ÊúâÊî∂Âà∞ÂìçÂ∫î
        console.error('  - ÈîôËØØ: Êú™Êî∂Âà∞ÊúçÂä°Âô®ÂìçÂ∫î„ÄÇ')
        console.error('  - ÊèêÁ§∫: ËØ∑Á°ÆËÆ§‰∏ªÁ®ãÂ∫è (src/index.ts) ÊòØÂê¶Â∑≤Âú®ËøêË°åÔºåÂπ∂‰∏îÁõëÂê¨ÁöÑÂú∞ÂùÄÂíåÁ´ØÂè£Ê≠£Á°Æ„ÄÇ')
      }
      else {
        // ËÆæÁΩÆËØ∑Ê±ÇÊó∂ÂèëÁîüÈîôËØØ
        console.error('  - ÈîôËØØ: ËØ∑Ê±ÇËÆæÁΩÆÂ§±Ë¥•„ÄÇ')
        console.error('  - ËØ¶ÊÉÖ:', axiosError.message)
      }
    }
    else {
      // ÂÖ∂‰ªñÊú™Áü•ÈîôËØØ
      console.error('  - ÂèëÁîüÊú™Áü•ÈîôËØØ:', error.message || error)
    }
  }
}

yargs(hideBin(process.argv))
  .scriptName('test-client')
  .command(
    'heartbeat',
    'Send heartbeat command',
    () => {},
    async () => {
      console.log('Sending [heartbeat] command...')
      await sendCommand(CommandCode.HEARTBEAT)
    },
  )
  .command(
    'start',
    'Send start scan command',
    () => {},
    async () => {
      console.log('Sending [start] command...')
      await sendCommand(CommandCode.START)
    },
  )
  .command(
    'stop',
    'Send stop scan command',
    () => {},
    async () => {
      console.log('Sending [stop] command...')
      await sendCommand(CommandCode.STOP)
    },
  )
  .command(
    'listen',
    'Listen for device events from the server',
    () => {},
    () => {
      console.log('Listening for events from server...')
      const es = new EventSource(`${BASE_URL}/events`)
      es.onmessage = (event: MessageEvent) => {
        console.log('üì©  Received event:')
        console.log(JSON.parse(event.data))
      }
      es.onerror = (err: ErrorEvent) => {
        if (err.type === 'error' && (err as any).status === 404) {
          console.error('‚ùå Êâæ‰∏çÂà∞ÊúçÂä°Âô®„ÄÇ‰∏ªÂ∫îÁî®Á®ãÂ∫è (src/index.ts) ÊòØÂê¶Ê≠£Âú®ËøêË°åÔºü')
        }
        else {
          console.error('‚ùå EventSource ÈîôËØØ:', err)
        }
        es.close()
      }
    },
  )
  .demandCommand(1, 'You need at least one command before moving on')
  .help()
  .parse()
